* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  min-height: 728px;
  background-color: black;
}

@media (max-width: 600px) {
  .App {
    min-height: 500px;
  }
}

@media (min-width: 601px) and (max-width: 1200px) {
  .App {
    min-height: 600px;
  }
}

@media (min-width: 1201px) {
  .App {
    min-height: 728px;
  }
}
